import { useState, useEffect, useCallback } from 'react';

interface UseSpeechRecognitionReturn {
  text: string;
  isListening: boolean;
  error: string | null;
  hasPermission: boolean | null;
  startListening: () => void;
  stopListening: () => void;
  pauseListening: () => void;
  resumeListening: () => void;
  clearText: () => void;
  setText: (text: string) => void;
  currentSpeaker: number;
  setSpeaker: (id: number) => void;
  compatibility: BrowserCompatibility;
}

interface BrowserCompatibility {
  isCompatible: boolean;
  isMobile: boolean;
  browser: string;
}

const checkBrowserCompatibility = (): BrowserCompatibility => {
  const userAgent = navigator.userAgent || navigator.vendor;
  
  //   
  const isMobile = /android|ipad|iphone|ipod/i.test(userAgent.toLowerCase());
  
  //  
  const isChrome = /chrome/i.test(userAgent);
  const isSafari = /safari/i.test(userAgent);
  const isFirefox = /firefox/i.test(userAgent);
  const isEdge = /edg/i.test(userAgent);
  
  let browser = '';
  if (isChrome) browser = 'Chrome';
  else if (isSafari) browser = 'Safari';
  else if (isFirefox) browser = 'Firefox';
  else if (isEdge) browser = 'Edge';

  //   -Speech Recognition
  const isCompatible = (
    ('webkitSpeechRecognition' in window) || 
    ('SpeechRecognition' in window)
  ) && (!isMobile || (isMobile && isChrome));

  return { isCompatible, isMobile, browser };
};

export const useSpeechRecognition = (): UseSpeechRecognitionReturn => {
  const [text, setText] = useState<string>('');
  const [isListening, setIsListening] = useState<boolean>(false);
  const [isPaused, setIsPaused] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [hasPermission, setHasPermission] = useState<boolean | null>(null);
  const [recognition, setRecognition] = useState<SpeechRecognition | null>(null);
  const [currentSpeaker, setCurrentSpeaker] = useState<number>(1);
  const [lastTranscript, setLastTranscript] = useState<string>('');
  const [compatibility, setCompatibility] = useState<BrowserCompatibility>({ 
    isCompatible: false, 
    isMobile: false, 
    browser: '' 
  });

  useEffect(() => {
    const compat = checkBrowserCompatibility();
    setCompatibility(compat);

    if (!compat.isCompatible) {
      if (compat.isMobile) {
        setError(
          '       Chrome. ' +
          '   Chrome    .'
        );
      } else {
        setError(
          ` ${compat.browser}    . ` +
          '   Chrome, Edge  Firefox .'
        );
      }
      return;
    }

    if (!('webkitSpeechRecognition' in window) && !('SpeechRecognition' in window)) {
      setError('     ');
      return;
    }

    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    const recognitionInstance = new SpeechRecognition();

    recognitionInstance.continuous = true;
    recognitionInstance.interimResults = true;
    recognitionInstance.lang = 'he-IL';

    let silenceTimeout: NodeJS.Timeout;
    let currentTranscript = lastTranscript;
    
    recognitionInstance.onresult = (event: any) => {
      clearTimeout(silenceTimeout);

      let fullTranscript = '';
      let interimTranscript = '';
      
      //     
      for (let i = 0; i < event.results.length; i++) {
        const result = event.results[i];
        let transcript = result[0].transcript.trim();

        //   
        if (result.isFinal) {
          //    
          transcript = transcript
            // 
            .replace(/\s+(| | )\s*/gi, '. ')
            //  
            .replace(/\s+(| )\s*/gi, '? ')
            //  
            .replace(/\s+(| )\s*/gi, '! ')
            // 
            .replace(/\s+(|)\s*/gi, ', ')
            // 
            .replace(/\s+()\s*/gi, ': ');

          //     
          transcript = transcript
            //     
            .replace(/^(|||||||||)\s+([^?]+)$/gi, '$1 $2?')
            //        
            .replace(/([^.!?]\s{1,}){5,}([^.!?])$/g, '$1$2.')
            //   
            .replace(/\s+/g, ' ');

          fullTranscript += transcript + ' ';
        } else {
          interimTranscript = transcript;
        }
      }

      //   
      if (!lastTranscript) {
        //  
        fullTranscript = ` ${currentSpeaker}: ${fullTranscript}`;
      } else {
        //   
        fullTranscript = lastTranscript + fullTranscript;
      }

      //     
      if (fullTranscript.match(/[.!?]\s*$/)) {
        silenceTimeout = setTimeout(() => {
          setLastTranscript(fullTranscript + `\n\n ${currentSpeaker}: `);
        }, 1500);
      }

      //  
      if (interimTranscript) {
        setText(fullTranscript + interimTranscript);
      } else {
        setLastTranscript(fullTranscript);
        setText(fullTranscript);
      }
    };

    recognitionInstance.onerror = (event) => {
      if (event.error === 'not-allowed') {
        setError('   ');
        setHasPermission(false);
      } else {
        setError(`  : ${event.error}`);
      }
      setIsListening(false);
    };

    setRecognition(recognitionInstance);

    navigator.mediaDevices.getUserMedia({ audio: true })
      .then(() => setHasPermission(true))
      .catch(() => {
        setHasPermission(false);
        setError('   ');
      });

    return () => {
      if (recognition) {
        recognition.stop();
      }
      clearTimeout(silenceTimeout);
    };
  }, [currentSpeaker, lastTranscript]);

  useEffect(() => {
    setText(lastTranscript);
  }, [lastTranscript]);

  const startListening = useCallback(() => {
    if (recognition && !isListening) {
      try {
        recognition.start();
        setIsListening(true);
        setIsPaused(false);
        setError(null);
      } catch (err) {
        setError('   ');
        setIsListening(false);
      }
    }
  }, [recognition, isListening]);

  const stopListening = useCallback(() => {
    if (recognition && isListening) {
      recognition.stop();
      setIsListening(false);
      setIsPaused(false);
    }
  }, [recognition, isListening]);

  const pauseListening = useCallback(() => {
    if (recognition && isListening && !isPaused) {
      recognition.stop();
      setIsPaused(true);
      setIsListening(false);
    }
  }, [recognition, isListening, isPaused]);

  const resumeListening = useCallback(() => {
    if (recognition && isPaused) {
      recognition.start();
      setIsPaused(false);
      setIsListening(true);
    }
  }, [recognition, isPaused]);

  const clearText = useCallback(() => {
    if (window.confirm('       ?')) {
      setText('');
      setLastTranscript('');
    }
  }, []);

  const setSpeaker = useCallback((id: number) => {
    setCurrentSpeaker(id);
    if (lastTranscript) {
      //      
      const newTranscript = lastTranscript.trim() + `\n\n ${id}: `;
      setLastTranscript(newTranscript);
      setText(newTranscript);
    }
  }, [lastTranscript]);

  return {
    text,
    isListening,
    error,
    hasPermission,
    startListening,
    stopListening,
    pauseListening,
    resumeListening,
    clearText,
    setText,
    currentSpeaker,
    setSpeaker,
    compatibility,
  };
}; 